.section__subtitle {
  color: var(--title-color);
}

.about__container {
  grid-template-columns: 1fr;
  row-gap: 2.5rem;
  align-items: center;
}

.about__data {
  text-align: center;
}

.about__img {
  width: 350px;
  border-radius: 1.5rem;
  justify-self: center;
  margin-left: 0;
}

.about__info {
  grid-template-columns: repeat(3, 1fr);

  gap: 0.5rem;
  margin-bottom: var(--mb-2);
}

.about__box {
  background-color: var(--container-color);
  border: 1.5px solid var(--border-box-color);
  border-radius: 0.75rem;
  text-align: center;
  padding: 1rem 1.25rem;
}

.about__icon {
  font-size: 1.5rem;
  color: var(--title-color);
  margin-bottom: var(--mb-0-5);
}

.about__title {
  font-size: var(--small-font-size);
  font-weight: var(--font-medium);
}

.about__subtitle {
  font-size: var(--small-font-size);
  color: var(--title-color-light);
}

.about__description {
  padding: 0 4rem 0 0;
  margin-bottom: var(--mb-2-5);
  color: var(--title-color);
}

/* ===================  BREAKPOINTS =================== */

/* For large devices */
@media screen and (max-width: 992px) {
  .about__img {
    width: 220px;
  }

  .about__box {
    padding: 0.75rem 0.5rem;
  }

  .about__info {
    justify-content: center;
  }

  .about__description {
    padding: 0 5rem;
    margin-bottom: 2rem;
  }
}

/* For medium devices */

@media screen and (max-width: 576px) {
  .about__info {
    grid-template-columns: repeat(3, 1fr);
  }

  .about__description {
    padding: 0;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .about__info {
    grid-template-columns: repeat(2, 1fr);
  }
}
